@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body , html{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}
.container{
    width: 400px;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0;
    padding: 0;
    height: 100vh;
}
.profile-container{
    margin:0;
    padding: 0;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.userName{
    margin:0;
    padding:0;
    font-size:1em;
    font-weight: 500;
}

.user-info{
    padding:0 10px;
}
.act-type{
    margin:0;
    font-size: 0.6em;
    font-weight: 400;
    text-align: left;


}
.icon{
    position: relative;
    right: -40px;
    top:-10px;
    margin-left: 20px;
    
}
.card-container {
    width:300px;
    overflow: hidden;
    height: 100px;    
    padding:4% 0;
    margin-bottom: 20px;
    border-bottom: 2px solid white;    
    border-radius: 20px;
}
.blue-card{
    background-color: rgb(34, 72, 240);
    box-shadow: 0 10px rgb(131, 162, 245);
    border-radius: 20px;
}
.red-card{
    background-color: rgb(240, 58, 34);
    box-shadow: 0 10px rgb(245, 158, 131);
    border-radius: 20px;
}
.card-element {
    display: inline-flex;
    display: inline-block;
    justify-content: center;
    align-items:center;
    padding: 2% 4% 0;
    margin: 0;    
    position: relative;
    top:-20px;
    color: white;
    text-align: left;
}
.card-title{
    font-size: 1em;
    padding: 0;
    margin:0;
    position: relative;
    top:-20px;
}
.card-sub-title{
    font-size: .8em;
    font-weight: 500;
    margin: 0;
    padding-top: 0;
    position: relative;
    top:-20px;

}
.timer{
    align-items: center;
    text-align: center;
    justify-content: center;
    font-size: 2em;
    padding: 0;
    margin:0;
    position: relative;
    top:20px
}
p{
    font-size: .7em;
    align-items: center;
    text-align: center;
}
.time {
    background-color: honeydew;
    text-align: center;
    height: 18px;
    width: 40px;
    border-radius: 5px;
    position: relative;
    top:-20px;
    color: grey;
}
h4{
    font-size: .7em;
    align-items: center;
    text-align: center;
    position: relative;
    top:-15px;

}
.card-icon{
    display: inline-flex;
    justify-content: right;
    align-items: flex-end;
    position: relative;
    top: -50px;
    right:-10px;

}
.signup-card-container{
    width:90%;
    overflow: hidden;
    height: 120px;    
    padding:4%;;
    margin-bottom: 20px;
    margin-top: 20px;
    border-bottom: 2px solid white;    
    border-radius: 20px;
    background-color: rgb(245, 230, 181);
    box-shadow: 0 10px rgb(211, 181, 82);
    color: rgb(235, 161, 23);
    border-radius: 20px;
}
.signup-card-element{
    display: inline-flex;
    display: inline-block;
    justify-content: center;
    align-items:center;
    padding: 2% 4% 0;
    margin: 0;    
    position: relative;
    top:-20px;
}
.card-img{
    align-items:center;
    justify-content: center;
    width: 100px;
    margin-top:30px;
    position: relative;
    right:50px;
    
}
.info{
    width: 120px;
    align-items: center;
    text-align: center;
    justify-content: center;
    font-size: .7em;
    padding: 0;
    margin:0;
    position: absolute;
    top:-100px;
    right:-50px;
}

.user-health{
    padding-top: 20px;
}
.health-item{
    justify-content: center;
    padding: 3%;
    align-items: center;
    
}
.value{
    justify-content: center;
    font-size: 2em;
    font-weight: 300;
}
.center{
    border-left:2px solid rgb(224, 224, 224);
    border-right:2px solid rgb(224, 224, 224);
    border-radius: 3px;
}
.title{
    font-size:.8em;
    padding: 5px;
}
.bar{
    width: 90%;
    height: 2px;
    background-color:rgb(184, 184, 184);
    margin: 10px 0;
}
.progress{
    height: 2px;
    margin: 10px 0;
}

.trainer-container{
    width: 300px;
    align-items: center;
    justify-content: center;
    text-align: left;
    margin: 0;
    padding:1% 3%;
    height: 100%;
    background-color:white;
}
.profile-container{
    margin-bottom:10px;   
    padding:0 1%;
    height: 100%;
    position: relative;
}

.trainer-name{
    position: absolute;
    bottom: 23px;
    margin:0;
    padding:0;
    font-size:.9em;
    font-weight: 500;
    text-align: left;
}

.trainer-info{
    padding:0 10px;
}
.trainer-sport{
    margin:0;
    font-size: 0.6em;
    text-align: left;
    font-weight: 400;


}
.icon{
    position: relative;
    right: -70px;
    top:-5px;
    margin-left: 20px;
    
}

.container{
    width: 300px;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin: 0;
    padding: 3%;
    height: 100%;
    background-color: #E9E9E9;
    border-radius: 20px;
}
.inline-div{
    display: inline-block;
}
h2{
    font-size: 1em;
    font-weight: 300;
    text-align: left;
    margin-top: 0;
}
